* {
  box-sizing: border-box;
}

body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  display: flex;
  position: relative;
}

@keyframes rotate {
  from {
    transform: translate(-50%)rotate(0);
  }

  to {
    transform: translate(-50%)rotate(360deg);
  }
}

@keyframes rotate2 {
  from {
    transform: translate(-50%, -100%)rotate(0);
  }

  to {
    transform: translate(-50%, -100%)rotate(360deg);
  }
}

.stopwatch {
  text-align: center;
  border: 1vmin dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minutes-hand {
  transform-origin: bottom;
  background-color: #0700ff;
  width: 3vmin;
  height: 20vmin;
  animation: 600s steps(60, end) infinite rotate2;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -100%)rotate(0);
}

.stopwatch__seconds-hand {
  transform-origin: bottom;
  background-color: #2c8000;
  width: 1.5vmin;
  height: 38vmin;
  animation: 10s linear infinite rotate;
  position: absolute;
  top: 10px;
  left: 50%;
  transform: translate(-50%)rotate(0);
}

.stopwatch__circle {
  z-index: 3;
  background-color: #f6a603;
  border-radius: 50%;
  width: 5vmin;
  height: 5vmin;
  position: absolute;
}
